body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Courier New", Courier, monospace;
  background: -webkit-linear-gradient(1deg, #FF5EDF 0%, #04C8DE 100%);
  background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{
    text-align: center;
}

.center{
    display: flex;
    justify-content: center;
}

.particles{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;
}
.Tilt{
    background: -webkit-linear-gradient(1deg, #FF5EDF 0%, #04C8DE 100%);
    background: linear-gradient(89deg, #FF5EDF 0%, #04C8DE 100%);
}
.form{
    width: 700px;
    background: 
  -webkit-linear-gradient(315deg, black 25%, transparent 25%) -50px 0,
  -webkit-linear-gradient(225deg, black 25%, transparent 25%) -50px 0,
  -webkit-linear-gradient(135deg, black 25%, transparent 25%),
  -webkit-linear-gradient(45deg, black 25%, transparent 25%);
    background: 
  linear-gradient(135deg, black 25%, transparent 25%) -50px 0,
  linear-gradient(225deg, black 25%, transparent 25%) -50px 0,
  linear-gradient(315deg, black 25%, transparent 25%),
  linear-gradient(45deg, black 25%, transparent 25%);	
  background-size: 2em 2em;
  background-color: #232323;
}



.bounding-box{
    position: absolute;
    box-shadow: inset 0 0 0 3px #149df2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    cursor: pointer;
}
